
.intro {
	text-align: center;
	max-width: 500px;
	margin-left: auto;
	margin-right: auto;
	padding: 10% 20px 7% 20px;

	.fullname {
		font-size: 1.6em;
		font-weight: 300; }

	.job {
		font-family: "Fira sans", sans-serif;
		font-size: 72px;
		font-weight: 700;
		line-height: 1.5em;
		@media #{$phone} {
			font-size: 52px; } }

	.descr {
		font-size: 18px;
		line-height: 1.6em;
		font-weight: 300;
		strong {
			font-weight: 500; } } }
