
.socials {
   padding-top: 10px; }

.linkedin,
.twitter,
.github {
   display: inline-block;
   width: 35px;
   height: 35px;
   margin: 10px;
   background-repeat: no-repeat;
   background-position: center center;
   background-size: 80%;
   transition: opacity .35s;
   opacity: .8;

   &:hover {
      //background-color: var(--color-main-500)
      opacity: 1; } }
